import React, { useEffect, useState } from "react";
import Checkbox from "@mui/material/Checkbox";
import {
  Grid,
  Container,
  FormControlLabel,
  TableCell,
  TableHead,
  TableRow,
  TableSortLabel,
  Table,
  TableBody
} from "@mui/material";
import Liquidity from "./views/liquidity/Liquidity";
export interface IPoint {
  price: string;
  size: number;
}
export interface IPool {
  id: string;
  time: string | Date;
  _100: IPoint[];
  _50: IPoint[];
  _25: IPoint[];
  _10: IPoint[];
  _5: IPoint[];
}
interface IOdds {
  id: string;
  time: string;
  _10xUp: number;
  _10xDown: number;
  _25xUp: number;
  _25xDown: number;
  _50xUp: number;
  _50xDown: number;
  _100xUp: number;
  _100xDown: number;
}

const App = () => {
  const [btcOdds, setBtcOdds] = useState<IOdds>();
  const [actualPool, setActualPool] = useState<IPool>();
  // const btcPrice = useRef<number>();
  const [btcPrice, setBtcPrice] = useState(null);
  const [_10xcb, set10xcb] = useState(false);
  const [_25xcb, set25xcb] = useState(false);
  const [_50xcb, set50xcb] = useState(false);
  const [_100xcb, set100xcb] = useState(false);
  const [upperPoolsSorted, setUpperPoolsSorted] = useState<IPoint[]>([]);
  const [downPoolsSorted, setDownPoolsSorted] = useState<IPoint[]>([]);

  const switchAll = () => {
    if (_10xcb && _25xcb && _50xcb && _100xcb) {
      set10xcb(false);
      set25xcb(false);
      set50xcb(false);
      set100xcb(false);
    } else {
      set10xcb(true);
      set25xcb(true);
      set50xcb(true);
      set100xcb(true);
    }
  };

  const searchActualPool = async () => {
    const search = await fetch("https://nodejsclusters-90686-0.cloudclusters.net/pools/bybitPools");
    if (search.status === 200) {
      const pool: IPool = await search.json();
      setActualPool(pool);
    }
  };
  const searchOdds = async () => {
    const search = await fetch(
      "https://nodejsclusters-90686-0.cloudclusters.net/pools/bybitBTCOdds"
    );
    if (search.status === 200) {
      const odds: IOdds = await search.json();
      setBtcOdds(odds);
    }
  };
  useEffect(() => {
    const ws = new WebSocket("wss://stream.bytick.com/realtime");
    ws.onopen = () => {
      ws.send('{"op":"subscribe","args":["trade.BTCUSD"]}');
    };
    ws.onmessage = function (event) {
      const info = JSON.parse(event.data);
      if (info.topic === "trade.BTCUSD") {
        const trades = info.data;
        const btcPrice = trades[trades.length - 1].price;
        setBtcPrice(btcPrice);
      }
    };
    searchActualPool();
    searchOdds();
    const interval = setInterval(searchActualPool, 150000);
    const intervalOdds = setInterval(searchOdds, 150000);
    return () => {
      ws.close();
      clearInterval(interval);
      clearInterval(intervalOdds);
    };
  }, []);

  useEffect(() => {
    if (!actualPool || !btcPrice) return;
    var _10 = [...(_10xcb ? actualPool._10 : [])];
    var _25 = [...(_25xcb ? actualPool._25 : [])];
    var _50 = [...(_50xcb ? actualPool._50 : [])];
    var _100 = [...(_100xcb ? actualPool._100 : [])];
    const pricesList = [..._10, ..._25, ..._50, ..._100];
    const priceMap = pricesList.reduce(
      (tot: any, act) => ({ ...tot, [act.price]: Number(tot?.price || 0) + act.size }),
      {}
    );
    const mergedList = Object.entries(priceMap).map(([price, size]) => ({ price, size }));
    const upperPoolsSorted = mergedList
      .filter(a => a.price > btcPrice)
      .sort((a: any, b: any) => b.size - a.size);
    const downPoolsSorted = mergedList
      .filter(a => a.price < btcPrice)
      .sort((a: any, b: any) => b.size - a.size);
    setUpperPoolsSorted(upperPoolsSorted as IPoint[]);
    setDownPoolsSorted(downPoolsSorted as IPoint[]);
  }, [_10xcb, _25xcb, _50xcb, _100xcb, actualPool]);

  return (
    <Container maxWidth="lg">
      <div>
        BTC Price: <b>{btcPrice}</b>
      </div>
      {actualPool && <div>{`Last Pool: ${new Date(actualPool.time).toLocaleString()}`}</div>}
      {btcOdds && (
        <table>
          <tr>
            <th>ODDS</th>
            <th>UP</th>
            <th>DOWN</th>
          </tr>
          <tr>
            <td>10X</td>
            <td>{btcOdds._10xUp}%</td>
            <td>{btcOdds._10xDown}%</td>
          </tr>
          <tr>
            <td>25X</td>
            <td>{btcOdds._25xUp}%</td>
            <td>{btcOdds._25xDown}%</td>
          </tr>
          <tr>
            <td>50X</td>
            <td>{btcOdds._50xUp}%</td>
            <td>{btcOdds._50xDown}%</td>
          </tr>
          <tr>
            <td>100X</td>
            <td>{btcOdds._100xUp}%</td>
            <td>{btcOdds._100xDown}%</td>
          </tr>
        </table>
      )}
      <Liquidity
        liquidityAbove={upperPoolsSorted.slice(0, 10)}
        liquidityBelow={downPoolsSorted.slice(0, 10)}
      />
      {btcPrice && (
        <>
          <Grid>
            <FormControlLabel
              control={<Checkbox onChange={switchAll} name="gilad" />}
              label="All/None"
              checked={_10xcb}
            />
            <FormControlLabel
              control={<Checkbox onChange={() => set10xcb(!_10xcb)} name="gilad" />}
              label="10x"
              checked={_10xcb}
            />
            <FormControlLabel
              control={<Checkbox onChange={() => set25xcb(!_25xcb)} name="gilad" />}
              label="25x"
              checked={_25xcb}
            />
            <FormControlLabel
              control={<Checkbox onChange={() => set50xcb(!_50xcb)} name="gilad" />}
              label="50x"
              checked={_50xcb}
            />
            <FormControlLabel
              control={<Checkbox onChange={() => set100xcb(!_100xcb)} name="gilad" />}
              label="100x"
              checked={_100xcb}
            />
          </Grid>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              UP
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>index</TableCell>
                    <TableCell>
                      <TableSortLabel>price</TableSortLabel>
                    </TableCell>
                    <TableCell>
                      <TableSortLabel direction="desc">size</TableSortLabel>
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {upperPoolsSorted.map((pool, i) => (
                    <TableRow key={i}>
                      <TableCell>{i}</TableCell>
                      <TableCell>{pool.price}</TableCell>
                      <TableCell>{pool.size}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Grid>

            <Grid item xs={6}>
              DOWN
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>index</TableCell>
                    <TableCell>
                      <TableSortLabel>price</TableSortLabel>
                    </TableCell>
                    <TableCell>
                      <TableSortLabel direction="desc">size</TableSortLabel>
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {downPoolsSorted.map((pool, i) => (
                    <TableRow key={i}>
                      <TableCell>{i}</TableCell>
                      <TableCell>{pool.price}</TableCell>
                      <TableCell>{pool.size}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Grid>
          </Grid>
        </>
      )}
    </Container>
  );
};

export default App;
