import React, { useEffect, useRef, useState } from "react";
import { createChart, IPriceLine, ISeriesApi } from "lightweight-charts";
import { IPoint } from "../../App";

interface ILiquidityProps {
  liquidityAbove: IPoint[];
  liquidityBelow: IPoint[];
}

const Liquidity = (props: ILiquidityProps) => {
  const ref = useRef() as React.MutableRefObject<HTMLInputElement>;
  //const [chart, setChart] = useState<IChartApi>();
  const [candlesArray, setCandlesArray] = useState<any[]>();
  const [candlestickSeries, setCandlestickSeries] = useState<ISeriesApi<"Candlestick">>();
  const [priceLines, setPriceLines] = useState<IPriceLine[]>();
  const searchCandles = async () => {
    // now - (200 candles of 5 minutes = 60000000) divided by 1000 to get the corresponding date
    const fromDate = Math.floor((Date.now() - 60000000) / 1000);
    const candlesFetch = await fetch(
      `https://api.bybit.com/v2/public/kline/list?symbol=BTCUSD&interval=5&limit=200&from=${fromDate}`
    );
    const candlesArray = await candlesFetch.json();
    setCandlesArray(candlesArray.result);
  };

  useEffect(() => {
    if (candlestickSeries && candlesArray) {
      candlestickSeries.setData(
        candlesArray.map((a: any) => ({
          time: a.open_time,
          high: +a.high,
          low: +a.low,
          open: +a.open,
          close: +a.close
        }))
      );
    }
  }, [candlesArray]);

  useEffect(() => {
    const chart = createChart(ref.current, {
      width: 800,
      height: 500
    });
    //setChart(chart);
    const candlestickSeries = chart.addCandlestickSeries();
    setCandlestickSeries(candlestickSeries);
    searchCandles();
  }, []);

  useEffect(() => {
    if (priceLines?.length) priceLines.map(pl => candlestickSeries?.removePriceLine(pl))
    if (candlestickSeries) {

      const priceLines: IPriceLine[] = [];
      props.liquidityAbove.map((liq, i) =>         priceLines.push(
        candlestickSeries.createPriceLine({
          price: +liq.price,
          color: "green",
          lineWidth: 1,
          lineStyle: 0,
          lineVisible: true,
          axisLabelVisible: true,
          title: (i + 1).toString()
        })
      ));

      props.liquidityBelow.map((liq, i) =>
        priceLines.push(
          candlestickSeries.createPriceLine({
            price: +liq.price,
            color: "red",
            lineWidth: 1,
            lineStyle: 0,
            lineVisible: true,
            axisLabelVisible: true,
            title: (i + 1).toString()
          }))
      );
      setPriceLines(priceLines)
    }
  }, [props.liquidityAbove, props.liquidityBelow, candlestickSeries]);  // eslint-disable-line

  return (
    <div>
      <div ref={ref} />
    </div>
  );
};

export default Liquidity;
